<template>
  <main>
    <h1>Your Files</h1>
    <p>These are the files you have uploaded</p>

    <table v-if="uploadedFiles.length!==0">
      <thead>
        <tr>
          <th>Name</th>
          <th>Size</th>
          <th>Upload Date</th>
          <th>Delete</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="file in uploadedFiles" v-bind:key="file.id">
          <td>{{file.name}}</td>
          <td>{{file.size}} byte</td>
          <td>{{file.uploadDate}}</td>
          <td @click="deleteFile(file.id)" class="delete">
            <img src="delete_button.png" alt="bin" />
          </td>
        </tr>
      </tbody>
    </table>

    <p v-else>
      You haven't load any file yet. Go
      <router-link class="router-link" to="/AddFile">here</router-link>to upload a file
    </p>
  </main>
</template>

<script scoped>
import Axios from "axios";

export default {
  name: "ManageFiles",
  methods: {
    deleteFile: function (idFile) {
      let rowToDelete = event.currentTarget.parentElement;
      Axios.delete(`${process.env.VUE_APP_APIROOT}uploaderarea/file/${idFile}`)
        .then(() => {
          this.uploadedFiles = this.uploadedFiles.filter(
            (current) => current.id !== idFile
          );
          rowToDelete.remove();
        })
        .catch(() => alert("Non Ã¨ stato possibile eliminare il file"));
    },
  },

  data: function () {
    return {
      uploadedFiles: [],
    };
  },

  created: function () {
    Axios.get(`${process.env.VUE_APP_APIROOT}uploaderarea/files`)
      .then((response) => (this.uploadedFiles = response.data))
      .catch((error) => console.log(error));
  },
};
</script>

<style scoped>
table {
  width: 70%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-flow: column nowrap;
  border: 2px solid grey;
  border-top-right-radius: 20px;
  border-top-left-radius: 20px;
  border-bottom-right-radius: 6px;
  border-bottom-left-radius: 6px;
}

table thead {
  height: 6vh;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.83);
  color: rgb(214, 139, 0);
  border-top-right-radius: inherit;
  border-top-left-radius: inherit;
}

table thead tr {
  flex-basis: 100%;
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}

table thead tr th {
  flex-basis: 25%;
  text-align: center;
}

table tbody {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  max-height: 55vh;
  overflow: auto;
}

table tbody tr {
  flex-basis: 12%;
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}

table tbody tr:nth-child(odd) {
  background-color: rgb(230, 230, 230);
}

table tbody tr td {
  flex-basis: 25%;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}

table tbody tr td img[alt="bin"] {
  height: 30px;
  width: auto;
  border: none;
}

table tbody td img:hover {
  cursor: pointer;
}

::-webkit-scrollbar {
  display: none;
}
</style>